<template>

    <div id="message_home">

        <div id="message_home_table">
            <div id="search">
                <el-autocomplete
                        popper-class="my-autocomplete"
                        v-model="state"
                        style="margin-right: 10px"
                        :fetch-suggestions="querySearch"
                        :placeholder = "$t('placeholder.content')"
                        @select="handleSelect"
                        @input="handleChange"
                        @keyup.enter.native="sendMessages">
                    <i
                            class="el-icon-edit el-input__icon"
                            slot="suffix"
                            @click="handleIconClick">
                    </i>
                    <template slot-scope="{ item }">
                        <div class="name">{{ item.username }}</div>
                        <span class="addr">{{ item.email }}</span>
                    </template>
                </el-autocomplete>
                <el-button size="medium" type="primary" @click="sendMessages">{{$t('button.search')}}</el-button>
            </div>

            <el-table
                    :data="myMessages"
                    border
                    @row-click="handle"
                    style="width: 100%">
                <el-table-column
                        :label="$t('column.date')"
                        width="180">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 10px">{{ scope.row.time }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        :label="$t('column.customer')"
                        width="180">
                    <template slot-scope="scope">
                        <el-avatar shape="circle" fit="scale-down" :src="scope.row.user.userimage" v-if="scope.row.user.userimage"></el-avatar><el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png" v-else></el-avatar>
                        <div style=" margin-right: 10px; line-height: 34.4px; float:left;">{{ scope.row.user.username }}</div>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="content"
                        :label="$t('column.newMessage')">
                </el-table-column>
                <el-table-column
                        :label="$t('column.operation')"
                        width="150">
                    <template slot-scope="scope">
                        <el-button size="medium" type="primary" @click="sendMessage(scope.row.user.username)">{{$t('button.sendMessage')}}</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>

</template>

